@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body{
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', sans-serif;
    color: purple;
}
.title{
  text-align: center;
  color: purple;
  font-size: 3.2vw;;
  font-weight: bold;
  font-family: 'Raleway', sans-serif;
}
.reactLogo{
  margin: 5px 10px 0px 0px;
  width: 40px;
  height: 40px;
}

@media only screen and (max-width: 768px) {
    /* For mobile: */
   #menuButton{
     display: inline;
     /* color: purple; */
    }
  .menu{
    display: none;
  }
  .title{
    text-align: center;
    color: purple;
    font-size: 6vw;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    align-self: center;
  }
}

.menu{
  right: 0;
  text-align: right;
  color: purple;
  font-family: 'Raleway', sans-serif;

}
table {
  table-layout: fixed ;
  width: 100% ;
}
.td1 {
  width: 25% ;
}
.td2 {
  width: 50% ;
}
.td3 {
  width: 25% ;
}
.mobileMenu{
    color: white;
    font-size: 28px;
    display: inline;
    line-height: 60px;
    list-style: none;
    text-align: center; 
}
.mobileMenu:focus{
    color: red;
}

.Dialog{
    background-color:rgba(36, 39, 44, 0.8)
}
.services{
        background-color: rgba(36, 39, 44, 0.9);
        border-radius: 0px solid black;
        color: white;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        padding: 0 5px;
        text-indent: 5px;
        height: 50px;
        font-size: 20px;
        text-align-last: center;
}
/* ./images/green-natural-leaves-2.jpg */
.header-div{
    background-image: url(/webflex-project/static/media/birdwall.94486e8b.jpg); /* The image used */
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 400px; /* You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  display: flex;
  padding: 50px;
  /* justify-content: center; */
  align-items: center;

}

.inner-div Button{
    width: 200px;
    height: 40px;
    margin-top: 5px;
}

@media only screen and (max-width: 768px) {
  .header-div{
    background-image: url(/webflex-project/static/media/birdwall.94486e8b.jpg); /* The image used */
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 400px; /* You must set a specified height */
  background-position: 40%; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  display: flex;
  padding: 50px;
  /* justify-content: center; */
  align-items: center;

}
}

.main-grid1, .main-grid2{
    width: 100%;
    /* padding: 80px 50px; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.grid1, .grid2{
    /* border: 3px solid red; */
    display: inline-flex;
    align-items: center;
    padding: 50px;
}
.main-grid2 .grid2 h1{
    font-size: 3.5em;
}
.main-grid3 .grid2, .main-grid4 h2{
    text-align: center;
    
}

.main-grid4 .grid1, .main-grid4 .grid2{
    padding: 10px 0;
}
.textfield{
    margin: 10px 0;
}
.paper{
    margin: 0 auto;
    padding-bottom: 20px;
    width: 90%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.paper div h2{
    text-align: left;    
}
.paper div label{
    font-weight: normal;
    font-size: 16px;
    border-radius: 50%;
    padding: 5px 10px;
    background-color: skyblue;
}
.paper div{
    text-align: left;
    padding: 20px;
}
.skill{
    width: 100%;
    height: 300px;
}
.join{
    width: 220px;
}
.slide-para{
    font-size: 22px;
    line-height: 28px;
}
.slide-img{
    /* border: 3px solid black; */
    width: 100%;
    height: 400px;
}
@media only screen and (max-width: 768px) {
    /* For mobile: */
    .main-grid2, .main-grid3{display: flex; flex-direction: column-reverse;}
    .main-grid1 .grid2{ padding: 6px;}
    .main-grid2 .grid1{ padding: 6px;}
    .main-grid1, .main-grid2{text-align: center;}   
    .slide-para{font-size: 21px; line-height: 30px;}
    .main-grid2 .grid2 h1{font-size: 2.3em;}
    .main-grid3 .grid1{padding: 0 5px;}
    .main-grid4 .paper{width: 95%; }
}
.upper-header{
    /* border: 1px solid black; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-image: linear-gradient(180deg,
     rgb(233, 242, 245) 14%,
     rgb(255, 255, 255) 99%) !important;
    color: rgb(172, 32, 172);
    padding: 30px 0;
     text-align: center;
     align-content: center;
    line-height: 25px;

}
.about{
    width: 140px;
    margin: 0 auto;
    /* font-weight: bold; */
    font-size: 15px;

}
.projects{
    line-height: 28px;

}
IconButton{
    display: block;
}
.lower-footer{
    /* border: 3px solid red; */
    background-color: #24272c;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    padding: 30px 0;
    text-align: center;
    color: black;
}
.lower-footer img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
